.burger {
  display: none; }
  @media (max-width: 768px) {
    .burger {
      display: none; } }
  @media (max-width: 320px) {
    .burger {
      display: inline-block;
      position: relative;
      height: 12px;
      width: 24px;
      padding: 0;
      cursor: pointer;
      border: none;
      background-color: transparent;
      z-index: 2; } }
  .burger__menu {
    display: block;
    position: relative;
    width: 24px;
    height: 2px;
    background: #f9fbfd;
    margin: 0 auto;
    transition: height 0.3s ease; }
    .burger__menu::after, .burger__menu::before {
      content: '';
      position: absolute;
      width: 24px;
      height: 2px;
      border: none;
      background: #f9fbfd;
      left: 0; }
    .burger__menu::before {
      top: -5px;
      transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
    .burger__menu::after {
      bottom: -5px;
      transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.burger.is-open {
  height: 20px; }

.burger__menu.is-open {
  background: rgba(255, 255, 255, 0); }

.burger__menu.is-open.burger__menu::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0, 1.55);
  background: #011c40; }

.burger__menu.is-open.burger__menu::after {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0, 1.55);
  background: #011c40; }

