.advantages {
  background: $color-solitude url('../../../img/background/advantages-background.png') no-repeat 0 0;
  background-size: cover;

  @include vp-768 {
    min-height: 588px;
    background: $color-solitude url('../../../img/background/advantages-background-tablet.png') no-repeat 0 0;
  }

  @include vp-320 {
    background: $color-solitude url('../../../img/background/advantages-background-mobile.png') no-repeat 0 0;
  }


  &__container {
    padding: 111px 103px 118px;

    @include vp-768 {
      padding: 90px 45px 100px;
    }

    @include vp-320 {
      padding: 65px 15px;
    }
  }

  &__title {
    font-size: 28px;
    font-weight: 600;
    line-height: 42px;
    text-align: center;
    margin: 0;
    margin-bottom: 60px;

    @include vp-768 {
      font-size: 26px;
      line-height: 39px;
      margin-bottom: 49px;
    }

    @include vp-320 {
      font-size: 24px;
      line-height: 36px;
      margin-top: -8px;
      margin-bottom: 31px;
    }
  }
}
