.directions {

  &__container {
    padding: 120px 103px;

    @include vp-768 {
      padding: 100px 45px;
    }

    @include vp-320 {
      padding: 65px 15px;
    }
  }

  &__title {
    font-size: 28px;
    font-weight: 600;
    line-height: 27px;
    text-align: center;
    margin: 0;
    margin-bottom: 55px;

    @include vp-768 {
      font-size: 26px;
      line-height: 39px;
      margin-top: -10px;
      margin-bottom: 49px;
    }

    @include vp-320 {
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 34px;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;

    display: grid;
    margin: 0 15px;
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    gap: 30px;

    @include vp-768 {
      grid-template-columns: repeat(auto-fill, minmax(309px, 1fr));
    }

    @include vp-320 {
      display: block;
      margin: 0 10px;
    }
  }
}
